import { useState, ChangeEventHandler } from 'react';

import Alert, { AlertColor } from '@mui/material/Alert';
import Box from '@mui/material/Box';
import Button from '@mui/material/Button';
import Stack from '@mui/material/Stack';
import TextField from '@mui/material/TextField';

import saveContent from '../../import';
import MainPanel from '../main-panel';

export default () => {
  const [content, setContent] = useState('');
  const [alertMessage, setAlertMessage] = useState('');
  const [severity, setSeverity] = useState('info' as AlertColor);
  const [inProgress, setInProgress] = useState(false);

  const updateImportContent: ChangeEventHandler<HTMLInputElement> = (event) => {
    setAlertMessage('');
    setContent(event.target.value || '');
  };

  const clearContent = () => setContent('');

  const importContent = async () => {
    setInProgress(true);
    setSeverity('warning');
    setAlertMessage('Import in progress...');

    try {
      await saveContent(content);
      setSeverity('success');
      setAlertMessage('Data imported successfully');
      setInProgress(false);
      setTimeout(() => setAlertMessage(''), 2000);
    } catch (e) {
      setSeverity('error');
      setAlertMessage(e instanceof Error ? e.message : String(e));
      setInProgress(false);
    }
  };

  const alertSection = alertMessage ? (
    <MainPanel.Section className="dano-import-section">
      <Alert variant="outlined" severity={severity}>
        {alertMessage}
      </Alert>
    </MainPanel.Section>
  ) : null;

  return (
    <MainPanel className="dano-importer" title="Data importer">
      <Box className="dano-importer-section" component="section">
        <TextField
          className="dano-importer-textarea"
          name="dano-importer-textarea"
          sx={{ fontSize: '12px' }}
          multiline
          rows={10}
          fullWidth
          onChange={updateImportContent}
          value={content}
          placeholder="Paste in the output from the bookmarklet."
          disabled={inProgress}
        />
      </Box>

      <MainPanel.Section className="dano-importer-section">
        <Stack className="dano-importer-buttons" spacing={2} direction="row">
          <Button onClick={importContent} disabled={!content || inProgress} variant="contained">
            Import
          </Button>
          <Button onClick={clearContent} disabled={!content || inProgress} variant="outlined">
            Clear
          </Button>
        </Stack>
      </MainPanel.Section>

      {alertSection}
    </MainPanel>
  );
};
